<template>
    <div v-if="ideatitystatus == 'teacher'">
        <div class="headdiv1">
            <h1 class="h1data"></h1>
            <HR class="hrline" align=center width=300 color=#5D998B SIZE=1></HR>
        </div>
        <el-form :inline="true" :model="addstudentForm" :rules="rules" ref="addstudentForm" label-width="100px" class="demo-ruleForm">

            <el-form-item  prop="username">
                <el-input size="medium" type="text" v-model="addstudentForm.username" placeholder=""></el-input>
            </el-form-item>
            <el-form-item  prop="sno">
                <el-input size="medium" type="text" v-model="addstudentForm.sno" placeholder=""></el-input>
            </el-form-item>
            <el-form-item  prop="password">
                <el-input size="medium" type="text" v-model="addstudentForm.password" placeholder=""></el-input>
            </el-form-item>
            <el-form-item>
                <el-button class="addbutton1" size="medium" type="primary" @click="submitForm('addstudentForm')"></el-button>
            </el-form-item>
            <el-form-item>
                <el-button class="resetbutton1" size="medium" type="primary" @click="resetForm('addstudentForm')"></el-button>
            </el-form-item>
        </el-form>

        <div class="headdiv2">
            <h1 class="h1data2"></h1>
            <HR class="hrline2" align=center width=300 color=#5D998B SIZE=1></HR>
        </div>

<!--        <div class="form-group" style="display: flex">-->
<!--            <img :src="imgCode" id="img-vcode"-->
<!--                 @click="getCodeImage" alt="" style="padding: 0 10px">-->
<!--            <input class="input-material" v-model="code" type="text"-->
<!--                   placeholder="">-->
<!--        </div>-->

        <el-row class="elrowselectresult">
            <el-col :span="10" class="elcolimage">
                <el-image :src="imgCode" id="img-vcode" @click="getCodeImage" alt="" style="padding: 0 10px"></el-image>
            </el-col>
            <el-col :span="1"><el-input class="input-material" v-model="code" type="text" placeholder=""></el-input></el-col>
            <el-col :span="2"><el-button class="elbuttonselectall" @click="IdentityCodeImage()" type="primary" size="medium"></el-button></el-col>
            <el-col :span="3"><el-input v-model="search" size="medium" placeholder="" class="elcolsearch"/></el-col>
        </el-row>



        <div>
            <el-table class="eltable1"
                      :data="tables"
                      :row-class-name="tableRowClassName"
                      :header-cell-style="{background:'#C7E2DC', 'text-align':'center'}"
                      :cell-style="{'text-align':'center'}"
                      style="width: 100%">
                <el-table-column
                        label=""
                        type="index"
                        width="50">
                </el-table-column>
                <el-table-column
                        prop="username"
                        label=""
                        width="180">
                </el-table-column>
                <el-table-column
                        label=""
                        width="180">
                    <template slot-scope="scope">
                        <el-popover trigger="hover" placement="top">
                            <p>: {{ scope.row.username}}</p>
                            <p>: {{ scope.row.password }}</p>
                            <div slot="reference" class="name-wrapper">
                                <el-tag size="medium" type="danger">{{ "" }}</el-tag>
                            </div>
                        </el-popover>
                    </template>
                </el-table-column>
                <el-table-column
                        prop="sno"
                        label="">
                </el-table-column>

                <!--            -->
                <el-table-column :show-overflow-tooltip="true" header-align="left" align="left">
                    <!--                -->
                    <!--                <template slot-scope="scope">-->
                    <!--                    {{scope.row}}-->
                    <!--                </template>-->


                </el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-button
                                size="mini"
                                @click="handleEdit(scope.row)"></el-button>
                        <el-button
                                size="mini"
                                type="danger"
                                @click="handleDelete(scope.row)"></el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>

    </div>
</template>

<script>
    import axios from "axios";
    import api from '../main';
    const $http = axios.create({
        baseURL: api.url,
        timeout: 1000,
        headers: {'X-Custom-Header': 'foobar'}
    });
    export default {
        name: "AddStudentnumber",
        data() {
            return {
                addstudentForm: {
                    username: '',
                    sno: '',
                    password: ''
                },
                addstudentreportcardForm: {
                    sno: '',
                    examstatus: '',
                    examgrade: '',
                    spendtime: ''
                },
                rules: {
                    username: [
                        {required: true, message: '', trigger: 'blur' }
                    ],
                    sno: [
                        {required: true, message: '', trigger: 'blur' }
                    ],
                    password: [
                        {required: true, message: '', trigger: 'blur' }
                    ]
                },
                imgCode: "",
                code: "",
                requestForm: "",
                tableData: [],
                search: '',
                msg: '',
                ideatitystatus: '',

            };
        },
        created() {
            this.ideatitystatus = sessionStorage.getItem('isteacher')
            // const _this = this;
            // axios.get('http://localhost:8181/findAll').then(function (resp) {
            //     console.log(resp);
            // })
            if(this.ideatitystatus == 'teacher')
            {
                this.getCodeImage();
            }else
            {
                this.$message.error('');
            }
        },
        computed: {
            // 
            tables() {
                const search = this.search;
                if (search) {
                    // filter() 
                    //  filter() 
                    //  filter() 
                    return this.tableData.filter(data => {
                        // some() ;
                        // some() 
                        // true , ;
                        // false
                        //  some() 
                        //  some() 
                        return Object.keys(data).some(key => {
                            // indexOf() -1
                            // toLowerCase()
                            return String(data[key]).toLowerCase().indexOf(search) > -1
                        })
                    })
                }
                return this.tableData
            }
        },
        methods: {
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        const _this = this;
                        axios.post(api.url + '/addstudentnumber', this.addstudentForm).then(function (resp) {
                            if(resp.data.state)
                            {
                                _this.$message.success(resp.data.msg);
                            }else
                            {
                                _this.$message.error(resp.data.msg);
                            }
                        });
                        this.addstudentreportcardForm.sno = this.addstudentForm.sno;
                        axios.post(api.url + '/studentreportcard/addstudentrepotcard', this.addstudentreportcardForm).then(function (resp1) {
                            if(resp1.data)
                            {
                                _this.$message.success(resp1.data);
                            }else
                            {
                                _this.$message.error(resp1.data);
                            }
                        });
                    } else {
                        this.$message.error("Please input correct data!")
                        return false;
                    }
                });
            },
            //
            async getCodeImage() {
                const {data: res} = await $http.get("/getImage");
                // console.log("" + res.data);
                console.log("base64", res);
                this.imgCode = "data:image/png;base64," + res;
            },
            IdentityCodeImage() {
                const _this = this;
                console.log(this.code);
                axios.get(api.url + '/findAll/' + this.code).then(function (resp) {
                    console.log(resp.data);
                    if(resp.data.state)
                    {
                        _this.tableData =resp.data.list;
                        console.log(_this.tableData);
                    }else
                    {
                        _this.$message.error("");
                    }

                })
            },
            handleEdit(row) {
                // console.log(index, row);
                // this.$router.push('/messageupdate') //
                //
                this.$router.push({
                    path: '/userupdate',
                    //
                    query: {
                        id: row.id
                    }
                })
            },
            handleDelete(row) {
                const _this = this;
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    axios.delete(api.url + '/deleteByUserId/' + row.id).then(function (resp) {
                        // console.log("resp.data:" + resp.data);
                        // _this.msg = resp.data;
                        if(resp.data == 1)
                        {
                            _this.$message.success("");
                        }else
                        {
                            _this.$message.error("");
                        }
                    });
                    // window.location.reload();//
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            },
            //
            tableRowClassName({row, rowIndex}) {
                let index = rowIndex + 1;
                if(index %2 == 0){
                    return 'warning-row'
                }
            }



        }
    }
</script>


