    /*    width: 50%;*/
    /*}*/
    .h1data, .h1data2 {
        /*margin-top: -74px;*/
        margin-left: -1131px;
        font-size: 18px;
        margin-top: 32px;
        color: #445f59;
    }
    .hrline, .hrline2 {
        width: 1200px;
        margin-top: 3px;
        font-size: 18px;
        margin-bottom: 30px;
    }
    .demo-ruleForm {
        margin-left: -21%;
    }
    .addbutton1, .elbuttonselectall {
        background-color: #239973;
    }
    .resetbutton1 {
        border: 1px solid #239973;
        background-color: #E9EEF3;
        color: #239973;
    }
    .eltable1 {
        width: 64%!important;
        margin: 0 auto;
        padding: 0!important;
    }
    .el-table .warning-row {
        background: #D7E4E1;
    }
    /*div*/
    .form-group {
        /*margin-top: 6%!important;*/
        /*margin-left: 105%!important;*/
    }
    .elcolsearch {
        margin-left: 266%;
    }
    .elcolimage {
        width: 5.66%;
        height: 1px!important;
        margin-left: 18%;
